import ObjectUtil from "@/util/ObjectUtil";

export default {

    dictIn: {
        '1': '',
        '2': '',
        '3': '',
        '4': '',
        '5': '',
    },

    dictOut: {
        '1': '',
        '2': '',
        '3': '',
        '4': '',
        '5': '',
    },

    colors: {
        '1': 'info',
        '2': 'warning',
        '3': 'success',
        '4': 'danger',
        '5': '',
    },

    list(isStockIn = true) {
        return ObjectUtil.dict2List(isStockIn ? this.dictIn : this.dictOut);
    }

}